function getTimeData() {
  let chinese = ["", "", "", "", "", "", ""];
  let d = [];
  for (let index = 0; index < chinese.length; index++) {
    d.push({
      value: index,
      label: "" + chinese[index],
      children: getLessonData(index)
    });
  }
  return d;
}
function getLessonData(i) {
  let d = [];
  for (let index = 1; index <= 12; index++) {
    d.push({
      value: i + "_" + index,
      label: "" + index + ""
    });
  }
  return d;
}
export default {
  getCourseData,
  getTimeData
};



