<template>
  <div>
    <h3 class="section-title"></h3>
    <el-button type="primary" :icon="canedit?'el-icon-edit':'el-icon-remove-outline'" @click="startEdit" v-if="!editmode" :disabled="!canedit">
      <template v-if="canedit"></template>
      <template v-else></template>
    </el-button>
    <template v-else>
      <el-button type="success" icon="el-icon-check" @click="finishEdit"></el-button>
      <el-button type="danger" icon="el-icon-close" @click="cancelEdit" plain></el-button>
    </template>
    <el-button type="primary" icon="el-icon-edit-outline" @click="editLogin"></el-button>
    <table class="profile" cellspacing="0">
      <tbody>
        <template v-for="(value,key) of profile">
          <tr v-if="key!='avatar'" :key="key">
            <th>{{name[key]}}</th>
            <td @click="edit(key)">
              <el-input @keydown.enter.native="editDone" v-if="clickEdit==key" v-model="editProfile[key]" :placeholder="name[value]"></el-input>
              <template v-else>
                <template v-if="key!='studentID' && editProfile.edit && editProfile[key]!=value">
                  <div class="profile-column">
                    <template v-for="item in distance(editProfile[key],value)">
                      <span :key="item.id+''" v-if="item.type=='add' || item.type=='different'" class="profile-add">{{item.word}}</span>
                      <span :key="item.id+''" v-if="item.type=='delete'" class="profile-delete">{{item.word}}</span>
                      <span :key="item.id+''" v-if="item.type=='equal'" class="profile-equal">{{item.word}}</span>
                    </template>
                  </div>
                </template>
                <template v-else>{{value}}</template>
              </template>
            </td>
            <td v-if="key=='name'" class="avatar" rowspan="5">
              <el-image :src="profile.avatar" fit="cover"></el-image>
            </td>
          </tr>
        </template>
      </tbody>
    </table>
    <h3 class="section-title"></h3>
    <el-table stripe :data="reawrdData" border style="width: 100%" class="reward">
      <el-table-column prop="date" label="" width="180"></el-table-column>
      <el-table-column prop="detail" label=""></el-table-column>
    </el-table>
    <el-dialog title="" :visible.sync="dialogVisible" width="30%">
      <el-form ref="form" :rules="rules" :hide-required-asterisk="true" :model="form" label-width="80px">
        <el-form-item label="" prop="password">
          <el-input placeholder="" v-model="form.password" show-password></el-input>
        </el-form-item>
        <el-form-item label="" prop="password2">
          <el-input placeholder="" v-model="form.password2" show-password></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="editLoginSubmit"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import editDistance from "@/utils/EditDistance";
export default {
  name: "profile",
  data() {
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.form.password) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    return {
      profile: {
        name: "",
        studentID: 2018999900000,
        faculty: "",
        major: "",
        class: "20189999",
        avatar: require("@/assets/img/avatar.png")
      },
      name: {
        name: "",
        studentID: "",
        faculty: "",
        major: "",
        class: ""
      },
      editProfile: {
        edit: false,
        name: "",
        faculty: "",
        major: "",
        class: "20189999"
      },
      clickEdit: "",
      editmode: false,
      canedit: true,
      dialogVisible: false,
      form: {
        password: "",
        password2: ""
      },
      rules: {
        password: [
          { required: true, message: "", trigger: "blur" }
        ],
        password2: [{ validator: validatePass2, trigger: "blur" }]
      },
      //
      reawrdData: [
        {
          date: "201411",
          detail: ""
        },
        {
          date: "201611",
          detail: ""
        },
        {
          date: "201771",
          detail: ""
        },
        {
          date: "201781",
          detail: ""
        },
        {
          date: "201781",
          detail: ""
        },
        {
          date: "201941",
          detail: "996"
        }
      ]
    };
  },
  methods: {
    edit(key) {
      if (!this.editmode || this.clickEdit != "") {
        return;
      }
      if (key == "studentID") {
        this.$message({ type: "error", message: "" });
        return;
      }
      this.clickEdit = key;
    },
    editDone() {
      this.clickEdit = "";
    },
    startEdit() {
      this.$message({ type: "info", message: "" });
      this.editmode = true;
      this.editProfile.edit = true;
    },
    finishEdit() {
      if (this.clickEdit != "") {
        this.$message({ type: "warning", message: "" });
        return;
      }
      this.$message({ type: "success", message: "" });
      this.editmode = false;
      this.canedit = false;
    },
    editLogin() {
      this.dialogVisible = true;
    },
    editLoginSubmit() {
      this.$refs.form.validate(valid => {
        if (!valid) {
          return false;
        }
        this.$message({ type: "success", message: "" });
        this.dialogVisible = false;
      });
    },
    cancelEdit() {
      this.editmode = false;
      this.editProfile.edit = false;
    },
    distance(str1, str2) {
      return editDistance(str1, str2);
    }
  },
  mounted() {
    this.global.constant.title = "";
  }
};
</script>
<style scoped>
.profile {
  width: 100%;
  border-collapse: collapse;
  background-color: #fff;
  border: 1px #eee solid;
  margin: 10px 0 0 0;
}
.profile tr {
  border-top: 1px #eee solid;
  transition: all 0.25s;
  height: 45px;
}
.profile tr:nth-child(2n) {
  background-color: #fafafa;
}
.profile tr:hover {
  background-color: #f5f7fa;
}
.profile tr:hover .avatar {
  background-color: #fff;
}
.profile tr:first-child {
  border-top: none;
}
.profile th {
  width: 100px;
  text-align: right;
  color: #909399;
}
.profile th::after {
  content: ":";
}
.profile td {
  padding: 0 0 0 5px;
  color: #606266;
}
.avatar {
  width: 200px;
  border-left: 1px #eee solid;
  background-color: #fff;
}

.reward {
  margin: 10px 0 0 0;
}
</style>




