<template>
  <div>
    <h3 class="section-title"></h3>
    <el-table :data="totalData" border style="width: 100%">
      <el-table-column prop="credit" label=""></el-table-column>
      <el-table-column prop="grade" label=""></el-table-column>
      <el-table-column prop="gpa" label=""></el-table-column>
    </el-table>
    <h3 class="section-title"></h3>
    <el-table :data="gradeData" border style="width: 100%">
      <el-table-column prop="code" label=""></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column prop="credit" label="" width="50"></el-table-column>
      <el-table-column prop="classify" label=""></el-table-column>
      <el-table-column prop="teacher" label=""></el-table-column>
      <el-table-column prop="grade.usual" label=""></el-table-column>
      <el-table-column prop="grade.final" label=""></el-table-column>
      <el-table-column prop="grade.gp" label=""></el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  name: "course",
  data() {
    return {
      totalData: [
        {
          credit: 0,
          grade: 0,
            gpa:0
        }
      ],
      gradeData: [
        {
          id: 0,
          name: "",
          classify: "",
          credit: 2,
          code: "LX000001",
          teacher: "",
          grade: {
            usual: 66,
            final: 65,
            gp:1.8
          }
        },
        {
          id: 1,
          name: "",
          classify: "",
          credit: 5,
          code: "LX000002",
          teacher: "",
          grade: {
            usual: 77,
            final: 33,
            gp:0
          }
        },
        {
          id: 2,
          name: "",
          classify: "",
          credit: 4,
          code: "LX000003",
          teacher: "",
          grade: {
            usual: 45,
            final: 66,
            gp:1.9
          }
        },
        {
          id: 3,
          name: "",
          classify: "",
          credit: 6,
          code: "LX000004",
          teacher: "",
          grade: {
            usual: 85,
            final: 85,
            gp:4
          }
        },
        {
          id: 4,
          name: "",
          classify: "",
          credit: 2,
          code: "LX000005",
          teacher: "",
          grade: {
            usual: 75,
            final: 65,
            gp:1.8
          }
        }
      ]
    };
  },
  methods: {
    calcTotal() {
      this.totalData[0].credit = 0;
      let tempGrade = 0,
        length = 0,
        tempGP = 0;
      for (const item of this.gradeData) {
        this.totalData[0].credit += item.credit;
        tempGrade += item.grade.final;
        tempGP += item.grade.gp;
        length++;
      }
      this.totalData[0].grade = (tempGrade / length).toFixed(1);
      this.totalData[0].gpa = (tempGP / length).toFixed(1);
    }
  },
  mounted() {
    this.global.constant.title = "";
    this.calcTotal();
  }
};
</script>
<style scoped>
</style>



