<template>
  <div>
    <el-button icon="el-icon-circle-plus-outline" @click="addStudentBtn" type="primary"></el-button>
    <el-button icon="el-icon-refresh-left" @click="refreshList"></el-button>
    <el-input v-model="search.name" class="search" prefix-icon="el-icon-search" placeholder="" v-if="!search.show"></el-input>
    <el-button :icon="search.show ? 'el-icon-close' : 'el-icon-search'" @click="searchBtn">
      <template v-if="!search.show"></template>
      <template v-else></template>
    </el-button>
    <div class="search-panel clearfix" :class="{ active: search.show }">
      <el-form class="search-panel-form" label-width="60px" :model="search">
        <el-form-item class="search-panel-item" label="">
          <el-input v-model="search.studentID"></el-input>
        </el-form-item>
        <el-form-item class="search-panel-item" label="">
          <el-input v-model="search.name"></el-input>
        </el-form-item>
        <el-form-item class="search-panel-item" label="">
          <el-input v-model="search.faculty"></el-input>
        </el-form-item>
        <el-form-item class="search-panel-item" label="">
          <el-input v-model="search.major"></el-input>
        </el-form-item>
      </el-form>
      <el-button class="search-panel-btn" icon="el-icon-search" @click="searchStart" type="success" v-if="search.show"></el-button>
    </div>
    <el-table border :data="studentData" stripe style="width: 100%;margin-top:10px;">
      <el-table-column prop="studentID" label=""></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column prop="faculty" label=""></el-table-column>
      <el-table-column prop="major" label=""></el-table-column>
      <el-table-column fixed="right" label="" width="180">
        <template slot-scope="scope">
          <el-button @click="editBtn(scope.row)" size="mini"></el-button>
          <el-button @click="deleteUser(scope.row)" type="danger" size="mini"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination class="pagination" background layout="prev, pager, next,jumper" :total="page.total" :current-page="page.now" :page-size="30" @current-change="changePage"></el-pagination>
    <el-dialog top="10vh" class="student-dialog" :title="tabPageName[tabPage]" :visible.sync="editDialogVisible">
      <el-tabs v-model="tabPage" @tab-click="tabClick">
        <el-tab-pane label="" name="edit">
          <el-form ref="editForm" :rules="rules" :hide-required-asterisk="true" :model="editForm" label-width="80px">
            <el-form-item class="student-edit-input" label="" prop="studentID">
              <el-input placeholder="" v-model="editForm.studentID"></el-input>
            </el-form-item>
            <el-form-item class="student-edit-input" label="" prop="name">
              <el-input placeholder="" v-model="editForm.name"></el-input>
            </el-form-item>
            <el-form-item class="student-edit-input" label="" prop="faculty">
              <el-input placeholder="" v-model="editForm.faculty"></el-input>
            </el-form-item>
            <el-form-item class="student-edit-input" label="" prop="major">
              <el-input placeholder="" v-model="editForm.major"></el-input>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="" name="grade">
          <el-table max-height="300" border :data="gradeData" stripe style="width: 100%;margin-top:10px;">
            <el-table-column prop="code" label=""></el-table-column>
            <el-table-column prop="name" label=""></el-table-column>
            <el-table-column prop="teacher" label=""></el-table-column>
            <el-table-column prop="grade" label=""></el-table-column>
            <el-table-column prop="gp" label=""></el-table-column>
            <el-table-column fixed="right" label="" width="100">
              <template slot-scope="scope">
                <el-popover placement="top" width="200" trigger="click">
                  <el-input @keydown.enter.native="setGrade(scope.row)" v-model="grade" placeholder=""></el-input>
                  <el-button slot="reference" size="mini"></el-button>
                </el-popover>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="" name="reward">
          <el-form :inline="true" :rules="rules" :model="reward">
            <el-form-item>
              <el-date-picker :editable="false" :clearable="false" v-model="reward.date" type="date" placeholder=""></el-date-picker>
            </el-form-item>
            <el-form-item>
              <el-input v-model="reward.detail" placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
              <el-button icon="el-icon-circle-plus-outline" @click="addNewReward" type="primary"></el-button>
            </el-form-item>
          </el-form>
          <el-table max-height="300" border :data="rewardData" stripe style="width: 100%;">
            <el-table-column width="180px" prop="date" label=""></el-table-column>
            <el-table-column prop="detail" label=""></el-table-column>
            <el-table-column fixed="right" label="" width="80">
              <template slot-scope="scope">
                <el-button @click="deleteReward(scope.row)" type="danger" slot="reference" size="mini"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="" name="reset">
          <el-form ref="reset" :rules="rules" :hide-required-asterisk="true" :model="reset" label-width="80px">
            <el-form-item label="" prop="password">
              <el-input placeholder="" v-model="reset.password" show-password></el-input>
            </el-form-item>
            <el-form-item label="" prop="password2">
              <el-input placeholder="" v-model="reset.password2" show-password></el-input>
            </el-form-item>
          </el-form>
        </el-tab-pane>
      </el-tabs>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"></el-button>
        <el-button v-if="tabPage == 'edit'" type="primary" @click="editSubmit"></el-button>
        <el-button v-if="tabPage == 'reset'" type="primary" @click="resetSubmit"></el-button>
      </span>
    </el-dialog>
    <el-dialog top="7vh" width="400px" title="" :visible.sync="addDialogVisible">
      <el-form ref="addForm" :rules="rules" :hide-required-asterisk="true" :model="addForm" label-width="45px">
        <el-form-item class="student-edit-input" label="" prop="studentID">
          <el-input placeholder="" v-model="addForm.studentID"></el-input>
        </el-form-item>
        <el-form-item class="student-edit-input" label="" prop="name">
          <el-input placeholder="" v-model="addForm.name"></el-input>
        </el-form-item>
        <el-form-item class="student-edit-input" label="" prop="faculty">
          <el-input placeholder="" v-model="addForm.faculty"></el-input>
        </el-form-item>
        <el-form-item class="student-edit-input" label="" prop="major">
          <el-input placeholder="" v-model="addForm.major"></el-input>
        </el-form-item>
        <el-form-item class="student-edit-input" label="" prop="password">
          <el-input placeholder="" v-model="addForm.password" show-password></el-input>
        </el-form-item>
        <el-form-item class="student-edit-input" label="" prop="password2">
          <el-input placeholder="" v-model="addForm.password2" show-password></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"></el-button>
        <el-button type="primary" @click="addSubmit"></el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "student-admin",
  data() {
    let validatePass2 = (rule, value, callback) => {
      console.log(value);
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.$refs[Object.keys(this.$refs)[0]].password) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    return {
      studentData: [
        {
          id: 0,
          name: "",
          studentID: 200000000,
          faculty: "",
          major: ""
        }
      ],
      page: {
        now: 1,
        total: 1000
      },
      editDialogVisible: false,
      addDialogVisible: false,
      search: {
        show: false,
        name: "",
        studentID: "",
        faculty: "",
        major: ""
      },
      editForm: {
        id: 0,
        name: "",
        studentID: "",
        faculty: "",
        major: ""
      },
      addForm: {
        id: 0,
        name: "",
        studentID: "",
        faculty: "",
        major: "",
        password: "",
        password2: ""
      },
      tabPage: "",
      tabPageName: {
        edit: "",
        reward: "",
        reset: "",
        grade: ""
      },
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        studentID: [{ required: true, message: "", trigger: "blur" }],
        faculty: [{ required: true, message: "", trigger: "blur" }],
        major: [{ required: true, message: "", trigger: "blur" }],
        password: [
          { required: true, message: "", trigger: "blur" }
        ],
        password2: [{ validator: validatePass2, trigger: "blur" }],
        date: [
          {
            type: "date",
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        detail: [{ required: true, message: "", trigger: "blur" }]
      },
      gradeData: [
        {
          code: "LX000005",
          name: "",
          teacher: "",
          grade: 85,
          gp: 4.0
        }
      ],
      grade: "",
      rewardData: [
        {
          date: "201411",
          detail: ""
        },
        {
          date: "201611",
          detail: ""
        },
        {
          date: "201771",
          detail: ""
        },
        {
          date: "201781",
          detail: ""
        },
        {
          date: "201781",
          detail: ""
        },
        {
          date: "201941",
          detail: "996"
        }
      ],
      reward: {
        date: "",
        detail: ""
      },
      reset: {
        password: "",
        password2: ""
      }
    };
  },
  methods: {
    //dialog
    addStudentBtn() {
      this.addDialogVisible = true;
    },
    //
    refreshList() {},
    //
    searchBtn() {
      this.search.show = !this.search.show;
    },
    //
    editBtn(item) {
      this.editForm.id = item.id;
      this.editForm.name = item.name;
      this.editForm.studentID = item.studentID;
      this.editForm.faculty = item.faculty;
      this.editForm.major = item.major;
      this.tabPage = "edit";
      this.editDialogVisible = true;
    },
    //
    editSubmit() {},
    //
    resetSubmit() {},
    //
    addSubmit() {},
    //
    setGrade() {},
    //
    deleteUser() {},
    //
    changePage() {},
    //
    searchStart() {},
    //tab
    tabClick() {},
    //
    deleteReward() {},
    //
    addNewReward() {}
  },
  mounted() {
    this.global.constant.title = "";
  }
};
</script>
<style scoped>
.pagination {
  text-align: right;
  margin-top: 5px;
}
.search {
  width: 200px;
  margin: 0 10px;
}
.search-panel {
  border: 1px #eee solid;
  margin: 10px 0;
  padding: 20px 40px 0 0;
  display: none;
  transition: all 0.25s;
}
.search-panel.active {
  display: block;
}
.search-panel-form {
  display: inline-block;
}
.search-panel-item {
  width: 200px;
  display: inline-block;
}
.search-panel-btn {
  float: right;
  margin-bottom: 10px;
}
.search-panel-input {
  width: 400px;
}
.student-edit-input {
  max-width: 500px;
}
</style>
<style>
.student-dialog {
  content: "";
}
.student-dialog .el-dialog__body {
  padding: 5px 20px 30px !important;
}
</style>



